/*your life as a painting*/

.gridContainer {
  min-width: 40%;

  position: relative;

  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 15%;
  overflow: visible;
}

.leftSideHeader {
  text-transform: uppercase;
  position: relative;
}
.rightSideText {
  margin-top: -10%;
  position: relative;
  margin-left: 15%;
  margin-right: 15%;
}

.containerTxt {
  margin-top: 0%;
  overflow: visible;
  width: 100%;
  display: flex;
}

#life {
  position: relative;
  rotate: -30deg;
  font-size: 15vw;
}
.lifeContainer {
  top: -9vw;
  left: 4.6%;
  position: absolute;

  overflow: visible;
}
#asA {
  opacity: 0;
  top: -8vw;
  right: 4%;
  position: absolute;
  rotate: 0deg;
  /* color:#00EEB9;*/
}
#painting {
  top: 2%;
  right: 5%;
  position: absolute;
  rotate: 0deg;
  font-size: 11vw;
}
/*paing not art section*/
.lifePaintGraphics {
  position: absolute;
  width: 35%;
  right: 0%;
}
.lamp {
  opacity: 1;
  /*
    top:-5vw;
    right:-18vw;
    */
  top: .2vw;
  right: -15vw;
  position: absolute;
  rotate: 58deg;
  z-index: -1;
  width: 70%;
}
#paintBrush {
  opacity: 1;
  /*right:99%;
    left:-8.2vw;
    top:-11.7%;
    */
  top: -92%;
  left: -8.7vw;
  rotate: -30deg;
  width: 130%;
  position: absolute;
}

#your {
  color: #00eeb9;
  position: relative;
  /*
    left:3.5%;
    top:5%;
    */

  rotate: 60deg;
}
#magnet {
  opacity: 0;
  transform-origin: bottom;
  top: -14vw;
  right: 32vw;
  position: absolute;
  rotate: 5deg;
  z-index: 100;
  width: 40%;
}
#lampNotOn {
  z-index: 1000;
}
#lampOn {
}
#paintingImg {
  margin-left: 47%;
  position: relative;
  opacity: 0.2;
  right: 3%;
  margin-top: -30%;
  width: 54%;
}
.brushContainer {
  scale: 1.1;

  rotate: -90deg;
  position: absolute;
  display: grid;
  grid-template-columns: 100%;
  overflow: visible;
  left: -100%;
  top: 30%;
}

